
#canvas{position:relative;height:100%;box-sizing:border-box
}
#canvas .loading{position:absolute;top:0;bottom:0;left:0;right:0;background:#fff;opacity:0.5
}
#canvas .content{position:relative
}
#canvas .content .elem-panel{position:absolute;top:0;left:0;overflow:hidden
}
#canvas .bgArea{background-position:center center
}
#canvas .loadingArea{position:absolute;top:0;left:0;background:#000;width:100%
}



